.shopContainer{
  display: flex;
}


  @keyframes blink {
    0%,
    22%,
    36%,
    75% {
      color: #ffe6ff;
      text-shadow: 0 0 0.6rem #fff4e6, 0 0 1.5rem #ffb765,
      -0.2rem 0.1rem 1rem #ffbc65, 0.2rem 0.1rem 1rem #ffbc65,
      0 -0.5rem 2rem #ff8724, 0 0.5rem 3rem #ff7c24;
    }
    28%,
    33% {
      color: #ffc965;
      text-shadow: none;
    }
    82%,
    97% {
      color: #ff7c24;
      text-shadow: none;
    }
  }
    .sign {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50%;
      height: 50%;
      background-image: radial-gradient(
        ellipse 50% 35% at 50% 50%,
        #6b4718,
        transparent
      );
      transform: translate(-50%, -50%);
      letter-spacing: 2;
      left: 47%;
      top: 15%;
      font-family: "Clip";
      text-transform: uppercase;
      font-size: 6em;
      color: #ffe6ff;
      text-shadow: 0 0 0.6rem #fff4e6, 0 0 1.5rem #ffb765,
          -0.2rem 0.1rem 1rem #ffbc65, 0.2rem 0.1rem 1rem #ffbc65,
          0 -0.5rem 2rem #ff8724, 0 0.5rem 3rem #ff7c24;
      animation: shine 2s forwards, flicker 3s infinite;
    }
  
  
  .flicker {
    animation: shine 2s forwards, blink 3s 2s infinite;
    margin-right: 10px;
    margin-left: 10px;
  }
  
  .fast-flicker {
    animation: shine 2s forwards, blink 10s 1s infinite;
    margin-right: 10px;
  }
  
  @keyframes shine {
    0% {
      color: #6b4a18;
      text-shadow: none;
    }
    100% {
      color: #ffe6ff;
      text-shadow: 0 0 0.6rem #fff4e6, 0 0 1.5rem #ffb765,
        -0.2rem 0.1rem 1rem #ffbc65, 0.2rem 0.1rem 1rem #ffbc65,
        0 -0.5rem 2rem #ff8724, 0 0.5rem 3rem #ff7c24;
    }
  }
  
  @keyframes flicker {
    from {
      opacity: 1;
    }
  
    4% {
      opacity: 0.9;
    }
  
    6% {
      opacity: 0.85;
    }
  
    8% {
      opacity: 0.95;
    }
  
    10% {
      opacity: 0.9;
    }
  
    11% {
      opacity: 0.922;
    }
  
    12% {
      opacity: 0.9;
    }
  
    14% {
      opacity: 0.95;
    }
  
    16% {
      opacity: 0.98;
    }
  
    17% {
      opacity: 0.9;
    }
  
    19% {
      opacity: 0.93;
    }
  
    20% {
      opacity: 0.99;
    }
  
    24% {
      opacity: 1;
    }
  
    26% {
      opacity: 0.94;
    }
  
    28% {
      opacity: 0.98;
    }
  
    37% {
      opacity: 0.93;
    }
  
    38% {
      opacity: 0.5;
    }
  
    39% {
      opacity: 0.96;
    }
  
    42% {
      opacity: 1;
    }
  
    44% {
      opacity: 0.97;
    }
  
    46% {
      opacity: 0.94;
    }
  
    56% {
      opacity: 0.9;
    }
  
    58% {
      opacity: 0.9;
    }
  
    60% {
      opacity: 0.99;
    }
  
    68% {
      opacity: 1;
    }
  
    70% {
      opacity: 0.9;
    }
  
    72% {
      opacity: 0.95;
    }
  
    93% {
      opacity: 0.93;
    }
  
    95% {
      opacity: 0.95;
    }
  
    97% {
      opacity: 0.93;
    }
  
    to {
      opacity: 1;
    }
  }
